import { Component, OnInit, ChangeDetectorRef, ChangeDetectionStrategy, Inject } from '@angular/core';
import { GlobalService } from '../../service/global.service';
import {MatTableModule} from '@angular/material/table';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

	export interface PeriodicElement {
	  train_number: string;
	  train_name: string;
	  train_type: string;
	}

  export interface DialogData {
    id: any,
    train_number: any,
    train_name: any,
    train_type: any
  }

@Component({
  selector: 'app-train-table',
  templateUrl: './train-table.component.html',
  styleUrls: ['./train-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TrainTableComponent implements OnInit {
	
  // setTrainList: any = [{train_number: 'Test1', train_name: 'Test2', train_type: 'Test3'}];
  trainList: any = [];
  displayedColumns: string[] = ['train_number', 'train_name', 'train_type'];
  constructor(private globalService: GlobalService, private changeDetectorRef: ChangeDetectorRef, public dialog: MatDialog) { }

  ngOnInit() {
    this.getTrainDetails();
  }
  getTrainDetails() {
    let payload = {url: 'train'};
    this.globalService.getService(payload);
    this.globalService.myGetSubject.subscribe((data) => {
      if (data && data.Train_Details) {
        let response: any = data.Train_Details;
        this.trainList = [];
        response.map((obj: any) => {
          let listDetail: any = {
            id: obj.id,
            train_number: obj.train_number,
            train_name: obj.train_name,
            train_type: obj.train_type
          }
          this.trainList.push(listDetail);
        });
        this.changeDetectorRef.markForCheck();
      }
    });
  }

   openUpdateTrainDeatils(type: any): void {
    let details: any = {
      id: ''
    };
    if (type === 'edit') {
      details = this.trainList[0];
    }
    
    const dialogRef = this.dialog.open(TrainTableDialog, {
      width: '450px',
      data: details
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result !== undefined) {
        this.getTrainDetails();
      }
    });
  }
  onDelete(): void {
    let payload = {url: 'train/48'};
    this.globalService.deleteService(payload);
    this.globalService.myDeleteSubject.subscribe((data) => {
      this.getTrainDetails();
    });
  }

  /* ngOnChanges() {
    console.log("OnChanges:ExampleComponent");
  }

  ngDoCheck() {
    console.log("....DoCheck:ExampleComponent");
  }

  ngAfterContentInit() {
    console.log("AfterContentInit:ExampleComponent");
  }

  ngAfterViewInit() {
    console.log("AfterViewInit:ExampleComponent");
  } */

}

@Component({
  selector: 'train-table-dialog',
  templateUrl: './train-table-dialog.html',
  styleUrls: ['./train-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TrainTableDialog {
  trainNumber: any = '';
  trainName: any = '';
  trainType: any = '';
  constructor(
    public dialogRef: MatDialogRef<TrainTableDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData, private globalService: GlobalService) {}
  ngOnInit() {
    if (this.data && this.data.id !== '') {
      this.trainNumber = this.data.train_number ? this.data.train_number : '';
      this.trainName = this.data.train_name ? this.data.train_name : '';
      this.trainType = this.data.train_type ? this.data.train_type : '';
    }
  }
  noThanks(): void {
    this.dialogRef.close();
  }

  onUpdate(): void {
    let request = {
      'train_number': this.trainNumber,
      'train_name': this.trainName,
      'train_type': this.trainType
    };
    let payload = {url: 'train', request: request};
    if (this.data && this.data.id !== '') {
      payload = {url: 'train/' + this.data.id, request: request};
      this.globalService.putService(payload);
      this.globalService.myPutSubject.subscribe((data) => {
      this.dialogRef.close(data);
    });
    } else {
      this.globalService.postService(payload);
      this.globalService.myPostSubject.subscribe((data) => {
      this.dialogRef.close(data);
    });
    }
    
    
  }
}