import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject, ReplaySubject } from 'rxjs';
import { map } from 'rxjs/operators';

export interface Todo {
  id: number | string;
  createdAt: number;
  value: string;
}

@Injectable({
  providedIn: 'root'
})
export class GlobalService {
  // private _todos = new BehaviorSubject<any>([]);
  public myGetSubject: ReplaySubject<any> = new ReplaySubject(1);
  public myPostSubject: ReplaySubject<any> = new ReplaySubject(1);
  public myPutSubject: ReplaySubject<any> = new ReplaySubject(1);
  public myDeleteSubject: ReplaySubject<any> = new ReplaySubject(1);
  private baseUrl = 'http://192.168.2.2:5000';
  // private dataStore: { todos: any } = { todos: [] };
  // readonly todos = this._todos.asObservable();
  headerConfig: any = {
      headers: {
        'Content-Type': 'application/json',
        'Cache-Control': 'no-cache',
        'Pragma': 'no-cache'
      }
    };
  constructor(private http: HttpClient) { }

  getService(payload: any) {
    this.http.get(`${this.baseUrl}/${payload.url}`).subscribe(data => {
      this.myGetSubject.next(data);
    }, error => console.log('Could not load.'));
  }

  postService(payload: any) {
    this.http.post(`${this.baseUrl}/${payload.url}`, payload.request, this.headerConfig).subscribe(data => {
      this.myPostSubject.next(data);
    }, error => console.log('Could not load.'));
  }

  putService(payload: any) {
    this.http.put(`${this.baseUrl}/${payload.url}`, payload.request, this.headerConfig).subscribe(data => {
      this.myPutSubject.next(data);
    }, error => console.log('Could not load.'));
  }

  deleteService(payload: any) {
    this.http.delete(`${this.baseUrl}/${payload.url}`, this.headerConfig).subscribe(data => {
      this.myDeleteSubject.next(data);
    }, error => console.log('Could not load.'));
  }
}